<template>
  <div class="detail-box" v-if="film">
    <div class="navbar">
      <van-nav-bar :title="film.name" safe-area-inset-top fixed placeholder left-arrow @click-left="onClickLeft" />
    </div>

    <div class="img">
      <img :src="film.poster" :alt="film.name" />
    </div>

    <div class="film-item">
      <div class="header">
        <div class="header-name">
          {{ film.name }} 
          <span class="item">{{ film.filmType.name }}</span>
        </div>
        <div class="grade">{{ film.grade||'' }} <span class="text"></span></div>
      </div>
      <div class="fs hh">{{ film.category }}</div>
      <div class="fs hh">{{ handleTime(film.premiereAt) }}</div>
      <div class="fs hh">{{ film.nation }}|{{ film.runtime }}</div>
      <div class="fs">
        <div v-if="isShowAll" class="van-multi-ellipsis--l2">{{ film.synopsis }}</div>
        <div v-else>{{ film.synopsis }}</div>
        <div class="arrow">
          <van-icon v-if="isShowAll" name="arrow-down" @click="isShowAll = !isShowAll" />
          <van-icon v-else name="arrow-up" @click="isShowAll = !isShowAll" />
        </div>
      </div>
    </div>

    <div class="film-actors">
      <div class="header"></div>
      <div class="swiper">
        <van-swipe :loop="false"  :width="100" :height="150" :show-indicators="false">
          <van-swipe-item v-for="(item,index) in film.actors" :key="index">
            <img :src="item.avatarAddress" :alt="item.role" />
            <div class="swiper-name">{{ item.name }}</div>
            <div class="swiper-role">{{ item.role }}</div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>

    <div class="photo">
      <div class="photo-title">
        <div class="juzhao"></div>
        <div class="all">({{ film.photos.length }})</div>
      </div>
      <van-swipe :loop="false" :width="170"  :show-indicators="false">
        <van-swipe-item  v-for="(item, index) in film.photos" :key="index" @click="showPhotos(film.photos, index)">
          <img :src="item" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <van-button class="btn" type="primary" block @click="showToast"></van-button>
  </div>
</template>

<script>
import { Toast } from 'vant';
import { ImagePreview } from 'vant'
import moment from 'moment'
export default {
  name: 'Detail',
  props: ['id'],
  data() {
    return {
      film: null,
      isShowAll: true,
    }
  },
  mounted() {
    this.getDetail()
  },
  methods: {
    async getDetail() {
      const { data: res } = await this.$api.GetMoviesDetail(this.id)
      // console.log(res);
      this.film = res.film
    },
    handleTime(time) {
      return moment(time * 1000).format('YYYY-MM-DD')
    },
    onClickLeft() {
      this.$router.back()
    },
    showPhotos(arr, index) {
      ImagePreview({
        images: arr,
        startPosition: index,
        closeable: true,
      })
    },
    showToast(){
      Toast('');
    }
  },
}
</script>

<style lang="less" scoped>
.detail-box {
  width: 100%;
  background-color: #ffffff;

  .img {
    height: 210px;
    // background-color: pink;
    overflow: hidden;
    img {
      width: 100%;
      position: relative;
      top: -140px;
    }
  }
  .film-item {
    // height: 190px;
    // background-color: red;
    padding: 15px;
    padding-top: 12px;

    .hh {
      height: 19px;
    }
    .fs {
      font-size: 15px;
      color: rgb(121, 125, 130);
      margin-top: 4px;
    }

    .header {
      display: flex;
      justify-content: space-between;
      .header-name {
        color: rgb(25, 26, 27);
        font-size: 18px;
        height: 24px;
        line-height: 24px;
        margin-right: 7px;
      }
      .grade {
        font-size: 18px;
        font-style: italic;
        color: rgb(255, 178, 50);
        .text {
          font-size: 10px;
        }
      }
      .item {
        font-size: 9px;
        color: rgb(255, 255, 255);
        background-color: rgb(210, 214, 220);
        height: 14px;
        line-height: 14px;
        padding: 0px 2px;
        border-radius: 2px;
        display: inline-block;
      }
    }

    .arrow {
      text-align: center;
    }
  }
  .film-actors {
    // height: 220px;
    // background-color: green;
    border-top: 10px solid #f4f4f4;
    border-bottom: 10px solid #f4f4f4;
    .header {
      height: 50px;
      padding: 15px;
      font-size: 16px;
    }
    .swiper {
      img {
        width: 100px;
        height: 117px;
        padding-left: 10px;
      }
      .swiper-name {
        font-size: 12px;
        color: rgb(25, 26, 27);
        text-align: center;
      }
      .swiper-role {
        font-size: 10px;
        color: rgb(121, 125, 130);
        text-align: center;
      }
    }
  }
  .photo {
    // height: 180px;
    border-bottom: 10px solid #f4f4f4;
    margin-bottom: 45px;
    .photo-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 62px;
      width: 100%;
      padding: 15px;
      // background-color: pink;
      .juzhao {
        font-size: 16px;
      }
      .all {
        font-size: 13px;
        color: rgb(121, 125, 130);
      }
    }
    img {
      width: 150px;
      height: 100px;
    }
  }
  .btn {
    position: fixed;
    bottom: 0;
  }
}
</style>
